@font-face {
    font-family: font1;
    src: url("./EduTASBeginner-VariableFont_wght.ttf");
}



@media only screen and (min-width:600px){
    .App{
        .search{
            input{
                width:500px;
            }
            button{
                position: absolute;
                right: 0;
            }
        }
        .forms{
            flex-direction: row;
            .colorEditors{
                flex-direction: column;
            }
            .form{
                width:600px;
                height: 500px;
            }
        }
        .form:nth-child(2){
            flex-direction: row;
        }
    }
}
.App{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    background-color: rgb(188, 169, 255);
    flex-wrap: wrap;
    min-height: 100vh;
    padding-left: 30px;
    padding-right: 30px;
    padding-top:30px;
    gap: 15px;
    .search{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: rgba(130, 95, 255,1);
        position: relative;
        input{
            height: 40px;
            outline: none;
            border: none;
            background-color: rgb(226, 202, 67);
            border:rgb(226, 202, 67) outset 5px;
            transition: 0.4s ease-in-out;
            color: rgb(248, 248, 248);
            font-size: 18px;
            padding: 5px;
            box-shadow: 5px 5px 10px rgba(0, 0, 0,0.5);
            &:hover,&:focus{
                background-color: rgb(255, 230, 87);
                border-color: rgb(255, 230, 87);
            }
        }
        button{
            height: 65px;
            width: 130px;
            font-size: 20px;
            font-family: font1;
            background-color: rgb(87, 109, 236);
            border:rgb(87, 109, 236) 5px outset;
            color: beige;
            transition: 0.4s ease-in-out;
            box-shadow: 3px 3px 6px rgba(0, 0, 0,0.5);
            &:hover{
                background-color: beige;
                color: rgb(87, 109, 236);
                border-color: beige;
            }
        }
    }
    .form{
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        z-index:21 ;
        gap:20px;
        background-color: rgb(130, 95, 255);
        border:5px outset rgb(130, 95, 255);
        box-shadow: 5px 5px 10px rgba(0, 0, 0,0.4);
        .title-input{
            width: 80%;
            height: 40px;
            padding-left: 10px;
            padding-right:10px ;
            background-color: rgb(0, 151, 113);
            color: beige;
            transition: 0.4s ease-in-out;
            outline: none;
            border: none;
            border-radius: 7px;
            &::placeholder{
                color: rgba(245, 245, 220, 0.61);
                transition: 0.4s ease-in-out;
            }
            &:focus,&:hover{
                transform: scale(1.1);
                

            }            
        }
        textarea{
            width: 80%;
            height: 50%;
            resize: none;
            padding: 10px;
            background-color: rgba(10, 75, 149,0.3);
            color: beige;
            outline: none;
            transition: 0.4s ease-in-out;
            border:none;
            border-radius: 7px;
            &::placeholder{
                color: rgba(245, 245, 220, 0.61);
            }
            &:focus,&:hover{
                transform: scale(1.1);
                

            }   
        }
        .create{
            background-color: rgba(10, 75, 149,1);
            border: none;
            padding: 10px;
            color: beige;
            border-radius: 7px;
            transition: 0.4s;
            width:100px;
            height: 50px;
            &:hover{
                background-color: beige;
                color: rgba(10, 75, 149,1);
            }
        }
    }
    .note{
        text-align: center;
        font-family: monospace;
        overflow: hidden;
        padding: 20px;
        scrollbar-color: #0A4C95 rgba(130, 95, 255,1) ;
        scrollbar-width: none;
        border-radius:2px;
        color: beige;
        font-size: 16px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        max-height: 350px;
        border: 7px outset ;
        transition: 0.4s;
        box-shadow: 5px 5px 10px rgba(0,0,0,0.5);
        margin-left: 10px;
        margin-bottom: 10px;
        h1{
            font-family: font1;
            position: relative;
            border: 3px outset rgb(0, 179, 95);
            padding: 5px;
            padding-left: 15px;
            padding-right: 15px;
            background-color: rgb(0, 179, 95);
        }

        pre{
            max-width: 100%;
            word-wrap: break-word;
            word-break: break-all;
            white-space: pre-wrap;
            font-family: monospace;
            text-align: start;
            color: rgb(0, 0, 0);
            overflow-y: scroll;
            padding-right: 3px;
            &::-webkit-scrollbar{
                width: 10px;
                background-color: attr(scrollbarColor);
            }
            &::-webkit-scrollbar-button{
                background-color: attr(scrollbarColor);
                color: red;
            }
            &::-webkit-scrollbar-thumb{
                background-color: rgb(91, 66, 180);
                border-radius: 15px;
            }
            &::-webkit-scrollbar-track-piece{
                background-color: attr(scrollbarColor);
            }

        }
        .buttons{
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;
            align-self: flex-end;
            button{
                border: 3px outset;
                padding: 10px;
                box-shadow: 3px 3px 6px rgba(0,0,0,0.5);

                &:nth-child(1){
                    color: beige;
                    transition: 0.4s ease-in-out;
                    &:hover{
                        &:active{
                            color: beige;
                            transition: none;
                        }
                    }
                } 
                &:nth-child(2){
                    transition: 0.4s ease-in-out;
                    &:hover{
                        &:active{
                            color: beige;
                        }
                    }
                } 
            }
        }
        
    }
}
@media only screen and (max-width:1100px){
    .App{
        justify-content: center;
    }
    .notes{
        justify-content: center;
        .note{
            resize: none;
        }
        
    }
    .search{
        gap:10px;
        input{
            width:200px;
        }
        button{
            position: relative;
            width:50px;
        }
    }
    .forms{
        flex-direction: column;
        justify-content: center;
        .colorEditors{
            flex-direction: row;
            input{
                height: 45px;
                width:15px;
            }
        }
        .form{
            flex-direction: column;
            width:80%;
            height: 80vh;
            &:nth-child(2){
                display: none;
            }
        }

    }
}
.my-masonry-grid {
    display: -webkit-box; /* Not needed if autoprefixing */
    display: -ms-flexbox; /* Not needed if autoprefixing */
    display: flex; /* gutter size offset */
    width: 100%;
    row-gap: 20px;
  }
  .my-masonry-grid_column { /* gutter size */
    background-clip: padding-box;
  }
